import React from 'react';
import { useSelector } from 'react-redux';

function Search() {
	const { items } = useSelector(({ products }) => products);

	const [searchBy, setSearchBy] = React.useState('');

	const query = window.location.hash;

	React.useEffect(() => {
		const replace = query.replace('#/search/?q=', '');
		setSearchBy(decodeURI(replace));
	}, [query]);

	const findFilter = items.filter((item) => item.title.toString().toLowerCase().includes(searchBy.toString().toLowerCase()));

	return (
		<>
			{searchBy !== '' ? `   "${searchBy}"` : "  "}

			{findFilter.map((item, key) => (
				<div key={key}>
					<img src={`./img/sections/products/${item.images[0].imageUrl}`} loading="lazy" alt={item.title} />
					{item.title}
				</div>
			))}
		</>
	)
}

export default Search;