import { Link } from "react-router-dom";

function MyMenu() {
	return (
		<nav className="menu">
			<button className="menu__burger _burger"><span></span></button>
			<div className="menu__body">
				<div className="menu__top menu-top">
					<Link className="header__logo header-logo" to="/">
						<img className="header-logo__icon" src="./img/logo.svg" alt="Logo" />
						<div className="header-logo__row">
							<strong className="header-logo__title">REACT FOOD</strong>
							<p className="header-logo__text">    </p>
						</div>
					</Link>
				</div>
			</div>
			<div className="menu__wrapper" />
		</nav>
	)
}

export default MyMenu;